<template>
  <div>
    <el-form
      :style='{"padding":"30px","boxShadow":"0 0px 0px rgba(64, 158, 255, .3)","margin":"-30px 0 0","borderRadius":"0 0 30px 30px","flexWrap":"wrap","background":"#F8F6F7","display":"flex","width":"100%"}'
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      label-width="120px"
    >  
     <el-row>
        <!--  -->
        <el-form-item :style='{"width":"100%","margin":"0 0 20px 0"}' v-if="flag=='users'" label="" prop="username">
          <el-input v-model="ruleForm.username" readonly placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"100%","margin":"0 0 20px 0"}' v-if="flag=='users'" label="" prop="name">
          <el-input v-model="ruleForm.name" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"100%","margin":"0 0 20px 0"}' v-if="flag=='users'" label="" prop="gender">
          <el-select v-model="ruleForm.gender" placeholder="">
            <el-option
                v-for="(item,index) in usersgenderOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item :style='{"width":"100%","margin":"0 0 20px 0"}' v-if="flag=='users'" label="" prop="age">
          <el-input v-model="ruleForm.age" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"100%","margin":"0 0 20px 0"}' v-if="flag=='users'" label="" prop="telephone">
          <el-input v-model="ruleForm.telephone" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"100%","margin":"0 0 20px 0"}' v-if="flag=='users'" label="" prop="avatar">
          <file-upload
            tip=""
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.avatar?ruleForm.avatar:''"
            @change="usersavatarUploadChange"
          ></file-upload>
        </el-form-item>

        <!--  -->
        <el-form-item :style='{"width":"100%","margin":"0 0 20px 0"}' v-if="flag=='admin'" label="" prop="username">
          <el-input v-model="ruleForm.username" placeholder=""></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item :style='{"width":"100%","margin":"0 0 20px 0"}' v-if="flag=='merchant'" label="" prop="merchantusername">
          <el-input v-model="ruleForm.merchantusername" readonly placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"100%","margin":"0 0 20px 0"}' v-if="flag=='merchant'" label="" prop="password">
          <el-input v-model="ruleForm.password" type="password" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"100%","margin":"0 0 20px 0"}' v-if="flag=='merchant'" label="" prop="merchantname">
          <el-input v-model="ruleForm.merchantname" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"100%","margin":"0 0 20px 0"}' v-if="flag=='merchant'" label="" prop="merchantaddress">
          <el-input v-model="ruleForm.merchantaddress" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"100%","margin":"0 0 20px 0"}' v-if="flag=='merchant'" label="" prop="email">
          <el-input v-model="ruleForm.email" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"100%","margin":"0 0 20px 0"}' v-if="flag=='merchant'" label="" prop="phonenumber">
          <el-input v-model="ruleForm.phonenumber" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"100%","margin":"0 0 20px 0"}' v-if="flag=='merchant'" label="" prop="merchantlicense">
          <file-upload
            tip=""
            action="file/upload"
            :limit="1"  
            :multiple="false"
            :fileUrls="ruleForm.merchantlicense?ruleForm.merchantlicense:''"
            @change="merchantlicenseUploadChange"
          ></file-upload>
        </el-form-item>

        <!--  -->
        <el-form-item :style='{"width":"100%","padding":"0","margin":"0"}'>
          <el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"0","background":"#BAD399","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' type="primary" @click="onUpdateHandler"> </el-button>
        </el-form-item>
      </el-row>
    </el-form>
  </div>
</template>

<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isPhone,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
      ruleForm: {},
      flag: '',
      adminFlag: false,
      usersgenderOptions: [],
    };
  },
  mounted() {
    var table = this.$storage.get("sessionTable");
    this.flag = table;
    // merchant
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
      } else {
        this.$message.error(data.msg);
      }
    });
    this.usersgenderOptions = ",".split(',')
  },
  methods: {
    // 
    usersavatarUploadChange(fileUrls) {
      this.ruleForm.avatar = fileUrls;
    },
    // 
    merchantlicenseUploadChange(fileUrls) {
      this.ruleForm.merchantlicense = fileUrls;
    },
    // 
    onUpdateHandler() {
      // ========== (users)  ==========
      if (this.flag === 'users') {
        if (!this.ruleForm.username) {
          this.$message.error('');
          return;
        }
        if (!this.ruleForm.password) {
          this.$message.error('');
          return;
        }
        if (!this.ruleForm.name) {
          this.$message.error('');
          return;
        }
        if (this.ruleForm.age && !isIntNumer(this.ruleForm.age)) {
          this.$message.error('');
          return;
        }
        if (this.ruleForm.telephone && !isMobile(this.ruleForm.telephone)) {
          this.$message.error('');
          return;
        }
        // URL
        if (this.ruleForm.avatar != null) {
          this.ruleForm.avatar = this.ruleForm.avatar.replace(new RegExp(this.$base.url, "g"), "");
        }
      }

      // ========== (admin)  ==========
      if (this.flag === 'admin') {
        if (!this.ruleForm.username || this.ruleForm.username.trim().length < 1) {
          this.$message.error('');
          return;
        }
      }

      // ========== (merchant)  ==========
      if (this.flag === 'merchant') {
        // 
        if (!this.ruleForm.merchantusername) {
          this.$message.error('');
          return;
        }
        if (!this.ruleForm.password) {
          this.$message.error('');
          return;
        }
        if (!this.ruleForm.merchantname) {
          this.$message.error('');
          return;
        }
        // 
        if (this.ruleForm.email && !isEmail(this.ruleForm.email)) {
          this.$message.error('');
          return;
        }
        if (this.ruleForm.phonenumber && !isMobile(this.ruleForm.phonenumber)) {
          this.$message.error('');
          return;
        }
        // URL
        if (this.ruleForm.merchantlicense != null) {
          this.ruleForm.merchantlicense = this.ruleForm.merchantlicense.replace(new RegExp(this.$base.url, "g"), "");
        }
      }

      // ==========  ==========
      this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
              // /
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.el-date-editor.el-input {
  width: auto;
}

.add-update-preview .el-form-item ::v-deep .el-form-item__label {
  padding: 0 10px 0 0;
  color: #666;
  font-weight: 500;
  width: 120px;
  font-size: 14px;
  line-height: 40px;
  text-align: center;
}

.add-update-preview .el-form-item ::v-deep .el-form-item__content {
  margin-left: 120px;
}

.add-update-preview .el-input ::v-deep .el-input__inner {
  border: 2px solid #797979;
  border-radius: 0;
  padding: 0 12px;
  box-shadow: 0 0 0px rgba(64, 158, 255, .5);
  outline: none;
  color: #000;
  width: 400px;
  font-size: 14px;
  height: 40px;
}

.add-update-preview .el-select ::v-deep .el-input__inner {
  border: 2px solid #797979;
  border-radius: 0;
  padding: 0 10px;
  box-shadow: 0 0 0px rgba(64, 158, 255, .5);
  outline: none;
  color: #000;
  width: 300px;
  font-size: 14px;
  height: 40px;
}

.add-update-preview .el-date-editor ::v-deep .el-input__inner {
  border: 2px solid #797979;
  border-radius: 0;
  padding: 0 10px 0 30px;
  box-shadow: 0 0 0px rgba(64, 158, 255, .5);
  outline: none;
  color: #000;
  width: 300px;
  font-size: 14px;
  height: 40px;
}

.add-update-preview ::v-deep .el-upload--picture-card {
  background: transparent;
  border: 0;
  border-radius: 0;
  width: auto;
  height: auto;
  line-height: initial;
  vertical-align: middle;
}

.add-update-preview ::v-deep .el-upload-list .el-upload-list__item {
  border: 2px solid #797979;
  cursor: pointer;
  border-radius: 6px;
  color: #797979;
  width: 90px;
  font-size: 32px;
  line-height: 90px;
  text-align: center;
  height: 90px;
}

.add-update-preview ::v-deep .el-upload .el-icon-plus {
  border: 2px solid #797979;
  cursor: pointer;
  border-radius: 6px;
  color: #797979;
  width: 90px;
  font-size: 32px;
  line-height: 90px;
  text-align: center;
  height: 90px;
}

.add-update-preview .el-textarea ::v-deep .el-textarea__inner {
  border: 2px solid #797979;
  border-radius: 0;
  padding: 12px;
  box-shadow: 0 0 0px rgba(64, 158, 255, .5);
  outline: none;
  color: #000;
  width: 400px;
  font-size: 14px;
  height: 120px;
}
</style>