	mounted(){
        this.pageFlag = this.$storage.get("pageFlag");
		let table = this.$storage.get("loginTable");
		this.tableName = table;
        this.usersgenderOptions = ",".split(',')
	},
	created() {
    
	},
	destroyed() {
		  	},
	methods: {
		// uuid
		getUUID () {
			return new Date().getTime();
		},
		close(){
			this.$router.push({ path: "/login" });
		},
        usersavatarUploadChange(fileUrls) {
            this.ruleForm.avatar = fileUrls;
        },

        // 


		// 
		login() {
			var url=this.tableName+"/register";
					if((!this.ruleForm.username) && `users` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
					if((!this.ruleForm.password) && `users` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
					if((this.ruleForm.password!=this.ruleForm.password2) && `users` == this.tableName){
						this.$message.error(``);
						return
					}
					if((!this.ruleForm.name) && `users` == this.tableName){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					
					if(`users` == this.tableName && this.ruleForm.age&&(!this.$validate.isIntNumer(this.ruleForm.age))){
						this.$message.error(``);
						return
					}
					
					
					
					
					
					
					
					
					
					
					
					
					
					if(`users` == this.tableName && this.ruleForm.telephone&&(!this.$validate.isMobile(this.ruleForm.telephone))){
						this.$message.error(``);
						return
					}
					
					
					
					
            if(this.ruleForm.avatar!=null) {
                this.ruleForm.avatar = this.ruleForm.avatar.replace(new RegExp(this.$base.url,"g"),"");
            }
					
					
					
					
					
					
					
					
					
					
				
			
			this.$http({
				url: url,
				method: "post",
				data:this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.$router.replace({ path: "/login" });
						}
					});
				} else {
					this.$message.error(data.msg);
				}
			});
		}
	}
};
