<template>
    <div class="content">
        <div class="text"
            :style='{ "margin": "50px auto", "fontSize": "24px", "color": "rgb(51, 51, 51)", "textAlign": "center", "fontWeight": "bold" }'>
             {{ this.$project.projectName }}</div>
            <div v-if="this.$storage.get('role') === ''" style="width: 100%">
            <div id="home-chart" style="height: 300px"></div>
        </div>
    </div>
    
</template>
<script>
//0
import router from '@/router/router-static'
import * as echarts from 'echarts'
export default {
    data() {
        return {
            charts: [],
            ydata: [],
            xdata: [],
            chartsdata: [],
            year: 2025,
        };
    },
    mounted() {
        this.init();
        this.getCharts();
        this.homeChart();
    },
    methods: {
        init() {
            if (this.$storage.get('Token')) {
                this.$http({
                    url: `${this.$storage.get('sessionTable')}/session`,
                    method: "get"
                }).then(({ data }) => {
                    if (data && data.code != 0) {
                        router.push({ name: 'login' })
                    }
                });
            } else {
                router.push({ name: 'login' })
            }
        },


        getCharts() {
            this.$http({
                url: "/ticketreserve/getCharts",
                method: "get",
                params: {
                    year: this.year,
                },
            }).then(({ data }) => {
                console.log(data);
                this.chartsdata = data.data;
                console.log(this.chartsdata);
                for (let key in this.chartsdata) {
                    this.xdata.push(key);
                    this.ydata.push(this.chartsdata[key]);
                    console.log(this.xdata);
                    console.log(this.ydata);
                }
                this.homeChart();
            });
        },
        extractMonth(dateString) {
            //  Date 
            const date = new Date(dateString);
            //  (0-111)
            const month = date.getMonth() + 1;
            //  ()
            return month < 10 ? `0${month}` : month.toString();
        },
        homeChart() {
            // domecharts
            var myChart = this.$echarts.init(document.getElementById("home-chart"));
            // 
            var option = {
                title: {
                    text: "", // 
                    left: "5%", // : 'center' | 'left' | 'right'
                    top: "10px", // : 
                    textStyle: {
                        color: "#333", // 
                        fontSize: 18, // 
                        fontWeight: "bold", // 
                    },
                },
                xAxis: {
                    data: this.xdata,
                },
                grid: {},
                tooltip: {
                    trigger: "axis",
                },
                yAxis: {
                    type: "value",
                },
                series: [
                    {
                        name: "",
                        type: "bar",
                        stack: "",
                        data: this.ydata,
                        barWidth: "30%",
                    },
                ],
            };


            // // 
            myChart.setOption(option);
            //
            window.onresize = function () {
                myChart.resize();
            };
        },
    }
};
</script>
<style lang="scss" scoped>
.cardView {
    display: flex;
    flex-wrap: wrap;
    width: 100%;

    .cards {
        display: flex;
        align-items: center;
        width: 100%;
        margin-bottom: 10px;
        justify-content: center;

        .card {
            width: calc(25% - 20px);
            margin: 0 10px;

            ::v-deep.el-card__body {
                padding: 0;
            }
        }
    }
}
</style>
