<template>
	<div class="addEdit-block" style="width: 100%;">
		<el-form
			:style='{ "padding": "30px", "boxShadow": "0 0px 0px rgba(64, 158, 255, .3)", "margin": "-30px 0 0", "borderRadius": "0 0 30px 30px", "flexWrap": "wrap", "background": "#F8F6F7", "display": "flex", "width": "100%" }'
			class="add-update-preview" ref="ruleForm" :model="ruleForm" :rules="rules" label-width="120px">
			<template>
				<el-form-item :style='{ "width": "100%", "margin": "0 0 20px 0" }' class="input" v-if="type != 'info'"
					label="" prop="merchantusername">
					<el-input v-model="ruleForm.merchantusername" placeholder="" clearable
						:readonly="ro.merchantusername"></el-input>
				</el-form-item>
				<el-form-item :style='{ "width": "100%", "margin": "0 0 20px 0" }' v-else class="input" label=""
					prop="merchantusername">
					<el-input v-model="ruleForm.merchantusername" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{ "width": "100%", "margin": "0 0 20px 0" }' class="input" v-if="type != 'info'"
					label="" prop="password">
					<el-input v-model="ruleForm.password" placeholder="" clearable :readonly="ro.password"></el-input>
				</el-form-item>
				<el-form-item :style='{ "width": "100%", "margin": "0 0 20px 0" }' v-else class="input" label=""
					prop="password">
					<el-input v-model="ruleForm.password" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{ "width": "100%", "margin": "0 0 20px 0" }' class="input" v-if="type != 'info'"
					label="" prop="merchantname">
					<el-input v-model="ruleForm.merchantname" placeholder="" clearable
						:readonly="ro.merchantname"></el-input>
				</el-form-item>
				<el-form-item :style='{ "width": "100%", "margin": "0 0 20px 0" }' v-else class="input" label=""
					prop="merchantname">
					<el-input v-model="ruleForm.merchantname" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{ "width": "100%", "margin": "0 0 20px 0" }' class="input" v-if="type != 'info'"
					label="" prop="merchantaddress">
					<el-input v-model="ruleForm.merchantaddress" placeholder="" clearable
						:readonly="ro.merchantaddress"></el-input>
				</el-form-item>
				<el-form-item :style='{ "width": "100%", "margin": "0 0 20px 0" }' v-else class="input" label=""
					prop="merchantaddress">
					<el-input v-model="ruleForm.merchantaddress" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{ "width": "100%", "margin": "0 0 20px 0" }' class="input" v-if="type != 'info'"
					label="" prop="email">
					<el-input v-model="ruleForm.email" placeholder="" clearable :readonly="ro.email"></el-input>
				</el-form-item>
				<el-form-item :style='{ "width": "100%", "margin": "0 0 20px 0" }' v-else class="input" label=""
					prop="email">
					<el-input v-model="ruleForm.email" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{ "width": "100%", "margin": "0 0 20px 0" }' class="input" v-if="type != 'info'"
					label="" prop="phonenumber">
					<el-input v-model="ruleForm.phonenumber" placeholder="" clearable
						:readonly="ro.phonenumber"></el-input>
				</el-form-item>
				<el-form-item :style='{ "width": "100%", "margin": "0 0 20px 0" }' v-else class="input" label=""
					prop="phonenumber">
					<el-input v-model="ruleForm.phonenumber" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{ "width": "100%", "margin": "0 0 20px 0" }' class="upload"
					v-if="type != 'info' && !ro.merchantlicense" label="" prop="merchantlicense">
					<file-upload tip="" action="file/upload" :limit="3" :multiple="true"
						:fileUrls="ruleForm.merchantlicense ? ruleForm.merchantlicense : ''"
						@change="merchantlicenseUploadChange"></file-upload>
				</el-form-item>
				<el-form-item :style='{ "width": "100%", "margin": "0 0 20px 0" }' class="upload"
					v-else-if="ruleForm.merchantlicense" label="" prop="merchantlicense">
					<img v-if="ruleForm.merchantlicense.substring(0, 4) == 'http'" class="upload-img"
						style="margin-right:20px;" v-bind:key="index" :src="ruleForm.merchantlicense.split(',')[0]"
						width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index"
						v-for="(item, index) in ruleForm.merchantlicense.split(',')" :src="$base.url + item" width="100"
						height="100">
				</el-form-item>
			</template>
			<el-form-item :style='{ "width": "100%", "padding": "0", "margin": "0" }' class="btn">
				<el-button
					:style='{ "border": "0", "cursor": "pointer", "padding": "0", "margin": "0 20px 0 0", "outline": "none", "color": "rgba(255, 255, 255, 1)", "borderRadius": "0", "background": "#BAD399", "width": "128px", "lineHeight": "40px", "fontSize": "14px", "height": "40px" }'
					v-if="type != 'info'" type="primary" class="btn-success" @click="onSubmit"></el-button>
				<el-button
					:style='{ "border": "0px solid rgba(64, 158, 255, 1)", "cursor": "pointer", "padding": "0", "margin": "0", "outline": "none", "color": "#fff", "borderRadius": "0", "background": "#BAD399", "width": "128px", "lineHeight": "40px", "fontSize": "14px", "height": "40px" }'
					v-if="type != 'info'" class="btn-close" @click="back()"></el-button>
				<el-button
					:style='{ "border": "0px solid rgba(64, 158, 255, 1)", "cursor": "pointer", "padding": "0", "margin": "0", "outline": "none", "color": "#fff", "borderRadius": "0", "background": "#BAD399", "width": "128px", "lineHeight": "40px", "fontSize": "14px", "height": "40px" }'
					v-if="type == 'info'" class="btn-close" @click="back()"></el-button>
			</el-form-item>
		</el-form>
	</div>
</template>
<script>
// url
import { isNumber, isIntNumer, isEmail, isPhone, isMobile, isURL, checkIdCard } from "@/utils/validate";
export default {
	data() {
		let self = this
		var validateIdCard = (rule, value, callback) => {
			if (!value) {
				callback();
			} else if (!checkIdCard(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validateUrl = (rule, value, callback) => {
			if (!value) {
				callback();
			} else if (!isURL(value)) {
				callback(new Error("URL"));
			} else {
				callback();
			}
		};
		var validateMobile = (rule, value, callback) => {
			if (!value) {
				callback();
			} else if (!isMobile(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validatePhone = (rule, value, callback) => {
			if (!value) {
				callback();
			} else if (!isPhone(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validateEmail = (rule, value, callback) => {
			if (!value) {
				callback();
			} else if (!isEmail(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validateNumber = (rule, value, callback) => {
			if (!value) {
				callback();
			} else if (!isNumber(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		var validateIntNumber = (rule, value, callback) => {
			if (!value) {
				callback();
			} else if (!isIntNumer(value)) {
				callback(new Error(""));
			} else {
				callback();
			}
		};
		return {
			id: '',
			type: '',


			ro: {
				merchantusername: false,
				password: false,
				merchantname: false,
				merchantaddress: false,
				email: false,
				phonenumber: false,
				merchantlicense: false,
				money: false,
			},


			ruleForm: {
				merchantusername: '',
				password: '',
				merchantname: '',
				merchantaddress: '',
				email: '',
				phonenumber: '',
				merchantlicense: '',
			},


			rules: {
				merchantusername: [
					{ required: true, message: '', trigger: 'blur' },
				],
				password: [
					{ required: true, message: '', trigger: 'blur' },
				],
				merchantname: [
					{ required: true, message: '', trigger: 'blur' },
				],
				merchantaddress: [
					{ required: true, message: '', trigger: 'blur' },
				],
				email: [
					{ validator: validateEmail, trigger: 'blur' },
				],
				phonenumber: [
					{ validator: validateMobile, trigger: 'blur' },
				],
				merchantlicense: [
				],
				money: [
					{ validator: validateNumber, trigger: 'blur' },
				],
			}
		};
	},
	props: ["parent"],
	computed: {



	},
	created() {
	},
	methods: {

		// 
		download(file) {
			window.open(`${file}`)
		},
		// 
		init(id, type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if (this.type == 'info' || this.type == 'else') {
				this.info(id);
			} else if (this.type == 'logistics') {
				this.logistics = false;
				this.info(id);
			} else if (this.type == 'cross') {
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj) {
					if (o == 'merchantusername') {
						this.ruleForm.merchantusername = obj[o];
						this.ro.merchantusername = true;
						continue;
					}
					if (o == 'password') {
						this.ruleForm.password = obj[o];
						this.ro.password = true;
						continue;
					}
					if (o == 'merchantname') {
						this.ruleForm.merchantname = obj[o];
						this.ro.merchantname = true;
						continue;
					}
					if (o == 'merchantaddress') {
						this.ruleForm.merchantaddress = obj[o];
						this.ro.merchantaddress = true;
						continue;
					}
					if (o == 'email') {
						this.ruleForm.email = obj[o];
						this.ro.email = true;
						continue;
					}
					if (o == 'phonenumber') {
						this.ruleForm.phonenumber = obj[o];
						this.ro.phonenumber = true;
						continue;
					}
					if (o == 'merchantlicense') {
						this.ruleForm.merchantlicense = obj[o];
						this.ro.merchantlicense = true;
						continue;
					}
					if (o == 'money') {
						this.ruleForm.money = obj[o];
						this.ro.money = true;
						continue;
					}
				}









			}


			// 
			this.$http({
				url: `${this.$storage.get('sessionTable')}/session`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {

					var json = data.data;
				} else {
					this.$message.error(data.msg);
				}
			});


		},
		// 

		info(id) {
			this.$http({
				url: `merchant/info/${id}`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.ruleForm = data.data;
					//
					let reg = new RegExp('../../../upload', 'g')//g
				} else {
					this.$message.error(data.msg);
				}
			});
		},


		// 
		onSubmit() {














			if (this.ruleForm.merchantlicense != null) {
				this.ruleForm.merchantlicense = this.ruleForm.merchantlicense.replace(new RegExp(this.$base.url, "g"), "");
			}



			var objcross = this.$storage.getObj('crossObj');

			//
			var crossuserid;
			var crossrefid;
			var crossoptnum;
			if (this.type == 'cross') {
				var statusColumnName = this.$storage.get('statusColumnName');
				var statusColumnValue = this.$storage.get('statusColumnValue');
				if (statusColumnName != '') {
					var obj = this.$storage.getObj('crossObj');
					if (statusColumnName && !statusColumnName.startsWith("[")) {
						for (var o in obj) {
							if (o == statusColumnName) {
								obj[o] = statusColumnValue;
							}
						}
						var table = this.$storage.get('crossTable');
						this.$http({
							url: `${table}/update`,
							method: "post",
							data: obj
						}).then(({ data }) => { });
					} else {
						crossuserid = this.$storage.get('userid');
						crossrefid = obj['id'];
						crossoptnum = this.$storage.get('statusColumnName');
						crossoptnum = crossoptnum.replace(/\[/, "").replace(/\]/, "");
					}
				}
			}
			this.$refs["ruleForm"].validate(valid => {
				if (valid) {
					if (crossrefid && crossuserid) {
						this.ruleForm.crossuserid = crossuserid;
						this.ruleForm.crossrefid = crossrefid;
						let params = {
							page: 1,
							limit: 10,
							crossuserid: this.ruleForm.crossuserid,
							crossrefid: this.ruleForm.crossrefid,
						}
						this.$http({
							url: "merchant/page",
							method: "get",
							params: params
						}).then(({
							data
						}) => {
							if (data && data.code === 0) {
								if (data.data.total >= crossoptnum) {
									this.$message.error(this.$storage.get('tips'));
									return false;
								} else {
									this.$http({
										url: `merchant/${!this.ruleForm.id ? "save" : "update"}`,
										method: "post",
										data: this.ruleForm
									}).then(({ data }) => {
										if (data && data.code === 0) {
											this.$message({
												message: "",
												type: "success",
												duration: 1500,
												onClose: () => {
													this.parent.showFlag = true;
													this.parent.addOrUpdateFlag = false;
													this.parent.merchantCrossAddOrUpdateFlag = false;
													this.parent.search();
													this.parent.contentStyleChange();
												}
											});
										} else {
											this.$message.error(data.msg);
										}
									});

								}
							} else {
							}
						});
					} else {
						this.$http({
							url: `merchant/${!this.ruleForm.id ? "save" : "update"}`,
							method: "post",
							data: this.ruleForm
						}).then(({ data }) => {
							if (data && data.code === 0) {
								this.$message({
									message: "",
									type: "success",
									duration: 1500,
									onClose: () => {
										this.parent.showFlag = true;
										this.parent.addOrUpdateFlag = false;
										this.parent.merchantCrossAddOrUpdateFlag = false;
										this.parent.search();
										this.parent.contentStyleChange();
									}
								});
							} else {
								this.$message.error(data.msg);
							}
						});
					}
				}
			});
		},
		// uuid
		getUUID() {
			return new Date().getTime();
		},
		// 
		back() {
			this.parent.showFlag = true;
			this.parent.addOrUpdateFlag = false;
			this.parent.merchantCrossAddOrUpdateFlag = false;
			this.parent.contentStyleChange();
		},
		merchantlicenseUploadChange(fileUrls) {
			this.ruleForm.merchantlicense = fileUrls;
		},
	}
};
</script>
<style lang="scss" scoped>
.amap-wrapper {
	width: 100%;
	height: 500px;
}

.search-box {
	position: absolute;
}

.el-date-editor.el-input {
	width: auto;
}

.add-update-preview .el-form-item ::V-deep .el-form-item__label {
	padding: 0 10px 0 0;
	color: #666;
	font-weight: 500;
	width: 120px;
	font-size: 14px;
	line-height: 40px;
	text-align: center;
}

.add-update-preview .el-form-item ::V-deep .el-form-item__content {
	margin-left: 120px;
}

.add-update-preview .el-input ::V-deep .el-input__inner {
	border: 2px solid #797979;
	border-radius: 0;
	padding: 0 12px;
	box-shadow: 0 0 0px rgba(64, 158, 255, .5);
	outline: none;
	color: #000;
	width: 400px;
	font-size: 14px;
	height: 40px;
}

.add-update-preview .el-select ::V-deep .el-input__inner {
	border: 2px solid #797979;
	border-radius: 0;
	padding: 0 10px;
	box-shadow: 0 0 0px rgba(64, 158, 255, .5);
	outline: none;
	color: #000;
	width: 300px;
	font-size: 14px;
	height: 40px;
}

.add-update-preview .el-date-editor ::V-deep .el-input__inner {
	border: 2px solid #797979;
	border-radius: 0;
	padding: 0 10px 0 30px;
	box-shadow: 0 0 0px rgba(64, 158, 255, .5);
	outline: none;
	color: #000;
	width: 300px;
	font-size: 14px;
	height: 40px;
}

.add-update-preview ::V-deep .el-upload--picture-card {
	background: transparent;
	border: 0;
	border-radius: 0;
	width: auto;
	height: auto;
	line-height: initial;
	vertical-align: middle;
}

.add-update-preview ::V-deep .upload .upload-img {
	border: 2px solid #797979;
	cursor: pointer;
	border-radius: 6px;
	color: #797979;
	width: 90px;
	font-size: 32px;
	line-height: 90px;
	text-align: center;
	height: 90px;
}

.add-update-preview ::V-deep .el-upload-list .el-upload-list__item {
	border: 2px solid #797979;
	cursor: pointer;
	border-radius: 6px;
	color: #797979;
	width: 90px;
	font-size: 32px;
	line-height: 90px;
	text-align: center;
	height: 90px;
}

.add-update-preview ::V-deep .el-upload .el-icon-plus {
	border: 2px solid #797979;
	cursor: pointer;
	border-radius: 6px;
	color: #797979;
	width: 90px;
	font-size: 32px;
	line-height: 90px;
	text-align: center;
	height: 90px;
}

.add-update-preview .el-textarea ::V-deep .el-textarea__inner {
	border: 2px solid #797979;
	border-radius: 0;
	padding: 12px;
	box-shadow: 0 0 0px rgba(64, 158, 255, .5);
	outline: none;
	color: #000;
	width: 400px;
	font-size: 14px;
	height: 120px;
}
</style>